<script>
    import { createEventDispatcher } from 'svelte';
    import Item from './AccordionChild.svelte'
    export let content;

    const dispatch = createEventDispatcher();

    const bubbleEvent = (i) => {
        return function (event) {
            dispatch('accordionClicked', {
                isOpen: event.detail.isOpen,
                index: i
            });
        }
    }
</script>

{#if typeof content === 'object' && Array.isArray(Object.entries(content))}
    {#each Object.entries(content) as entry, i}
    <Item {entry} on:accordionClicked={bubbleEvent(i)}  />
    {/each}
{/if}