<script>
	import {createEventDispatcher} from 'svelte';
	const dispatch = createEventDispatcher();
	export let data, selected, type;
</script>

{#if type && data}
<h3 data-testid="mainCheckboxGroupLabel">{@html type && '<h3>'+type.name+'</h3>'}</h3>
<div class={type.classes} style={type.style}>
	{#each data as {id, label, name, value}}
		<div class="boxes">
			<input type="checkbox" {id} {name} {value} bind:group={selected} on:change={() => dispatch('query', {params: selected, type: id})}>
			<label for={id}>{label}</label>
        </div>
	{/each}
</div>
{/if}

