<script>
    import { writable } from 'svelte/store';
    import LeftControl from './LeftControl.svelte';
    import RightControl from './RightControl.svelte';
    import RightControlEnd from './RightControlEnd.svelte';
    import LeftControlEnd from './LeftControlEnd.svelte';
    import { setContext } from 'svelte';
    import { onMount } from 'svelte';
    import { createEventDispatcher } from 'svelte';
    export let current = 1;
    export let per_page = 5;
    export let rounded = false;
    export let small = false;
    export let color = 'ashley-orange';
    export let content = '';
    export let total_products = 0;
    const selectedPage = writable(null);
    const content_slab = writable(null);
    const dispatch = createEventDispatcher();
    const contentLength = total_products ? total_products : content.length;
    let paging_start, paging_end;
    setContext('Pager', {
        content_slab,
        selectedPage,
    });

    $: s = small ? 8 : 12;
    $: num_pages = Math.ceil(contentLength / per_page);
    $: num_items = contentLength;
    $: content_slice = content.slice(current * per_page - per_page, current * per_page);
    let arr_pages = [];
    function buildArr(c, n) {
        if (n <= 7) {
            return [...Array(n)].map((_, i) => i + 1);
        } else {
            if (c < 4) {
                return [1, 2, 3, 4, 5];
            } else if (c > n - 3) {
                return [n - 4, n - 3, n - 2, n - 1, n];
            } else {
                return [c - 2, c - 1, c, c + 1, c + 2];
            }
        }
    }

    function setArrPages() {
        arr_pages = buildArr(current, num_pages);
    }
    $: if (current) {
        let comparitor, computedDifference;

        const unsubscribe = selectedPage.subscribe(value => {
            comparitor = value;
        });
        /* This logic determines the number of API pages skipped 
        - the proper value for deduction at the other end */
        let difference = current - comparitor;
        computedDifference = Math.floor(difference / 4) - 1;

        content_slab.set(content_slice);
        selectedPage.set(current);
        if ($content_slab.length >= per_page) {
            if (current == 1) {
                paging_start = 1;
                paging_end = per_page;
            } else {
                paging_start = current * per_page - per_page + 1;
                paging_end = current * per_page;
            }
        } else {
            paging_start = contentLength - $content_slab.length + 1;
            paging_end = contentLength;
        }
        setArrPages();
        dispatch('page_data', {
            difference: computedDifference,
            current: current,
            total_items: num_items,
            page_start: paging_start,
            page_end: paging_end,
        });
    }
    $: if (per_page) {
        setArrPages();
        current = 1;
    }
    $: if (num_pages) {
        num_pages = Math.ceil(num_items / per_page);
    }
    $: if (content) {
        content_slice = content.slice(current * per_page - per_page, current * per_page);
        num_items = contentLength;
        current = current || 1;
    }
    function setCurrent(i) {
        if (isNaN(i)) return;
        current = i;
    }
    onMount(() => {
        dispatch('page_data', {
            total_items: num_items,
            page_start: paging_start,
            page_end: paging_end,
        });
    });
</script>

<div class="pagination-results">
    <slot />
    <div class="flex text-{small ? 'base' : 'lg'}">
        {#if current != 1}
            <div class="h-{s} w-{s} flex left control {rounded ? 'rounded-full' : ''}
      {current < num_pages ? 'cursor-pointer' : 'text-gray-400'}" on:click={() => setCurrent(1)}>
                <LeftControlEnd class="w-{s / 2} h-{s / 2}" />
            </div>
            <div class="h-{s} w-{s} mr-1 flex left control {rounded ? 'rounded-full' : 'square'}" on:click={() => current > 1 && setCurrent(current - 1)}>
                <LeftControl class="w-{s / 2} h-{s / 2}" />
            </div>
        {/if}
        <div class="flex h-{s} {rounded ? 'rounded-full' : ''}">
            {#each arr_pages as i}
                <div
                    class="w-{s} hidden page-button-shape {i == current
                        ? rounded
                            ? `rounded-full bg-${color} current`
                            : `border-t-2 border-${color} current`
                        : rounded
                        ? 'rounded-full '
                        : 'border-t-2 border-white'}"
                    on:click={() => setCurrent(i)}>
                    {i}
                </div>
            {/each}
        </div>
        {#if current != num_pages}
            <div
                class="h-{s} w-{s} flex control {rounded ? 'rounded-full' : ''}
          {current < num_pages ? 'cursor-pointer' : 'text-gray-400'}"
                on:click={() => current < num_pages && setCurrent(current + 1)}>
                <RightControl class="w-{s / 2} h-{s / 2}" />
            </div>
            <div
                class="h-{s} w-{s} flex control {rounded ? 'rounded-full' : ''}
      {current < num_pages ? 'cursor-pointer' : 'text-gray-400'}"
                on:click={() => current < num_pages && setCurrent(num_pages)}>
                <RightControlEnd class="w-{s / 2} h-{s / 2}" />
            </div>
        {/if}
    </div>
</div>

