<script>
  import {fade} from 'svelte/transition';
  let isOpen = false;
  export let style = "";
  const toggleTooltip = () => isOpen = !isOpen;
</script>

<div class="tooltip" on:mouseleave={toggleTooltip} on:mouseenter={toggleTooltip} data-testid="mainTooltip">
  <div class="tooltip-content" aria-expanded={isOpen} transition:fade {style}>
    <slot/>
  </div>
  <slot name="trigger" />
</div>

