<script>
	import { createEventDispatcher } from 'svelte';
	export let tabs
	let activeTab = 0;
	const dispatch = createEventDispatcher();
	const toggle = (index) => {
		activeTab = index;
		dispatch('tabClicked', {
			activeTab: index
		});
	}
</script>
{#if tabs}
	<div class="tabs" data-testid="mainTabs">
		<div class="titles">
			{#each tabs as tab, index}
				<div class="tab">
					<span on:click={()=>toggle(index)} class:active={index == activeTab}>
						<slot name="tab-title">{tab.title}</slot>
					</span>
				</div>
			{/each}
		</div>
		<div class="content">
			<slot {activeTab} />
		</div>
	</div>
{/if}
