<script>
	export let qty, maxqty
	
	$: if (qty > maxqty) {
		qty = maxqty
	} else if (qty < 1) {
		qty = 1
	}
</script>
<div class="quantity-selector" data-testid="mainQuantity">
	<button on:click={()=>qty--}>-</button>
	<input type="text" bind:value={qty} />
	<button on:click={()=>qty++}>+</button>
</div>

