<script>
    import { ArrowCarouselLeftNewIcon, ArrowCarouselRightNewIcon, ArrowDownIcon } from '../SVG';
    import ZoomImg from '../ZoomImg/ZoomImg.svelte';
    import { onMount } from 'svelte';
    export let entries, style, width, height;

    let arrowLeft, arrowRight, baseCarousel, navLeft, navRight, carouselContent, carouselWidth, carouselNav, contentWidth, slidePos = 0, isQuickview = false;

    let fader = (e, pos) => {
        var prev = document.getElementsByClassName('fade')[0];
        var nav = document.getElementsByClassName('nav');
        prev.classList.remove('fade');
        if (e) {
            e.currentTarget.classList.add('fade');
        } else {
            nav[pos].classList.add('fade');
        }
    }
    let scroller = (slidePos) => {
        var nav = document.getElementsByClassName('nav');
        var slideWidth = nav[0].offsetWidth;
        var contentWidth = carouselWidth.offsetWidth / entries.length;
        var slideAmt = slideWidth*slidePos;
        var contentPos = contentWidth*slidePos;
        carouselNav.scrollTo({top: 0, left: slideAmt, behavior: 'smooth'});
        carouselContent.scrollTo({top: 0, left: contentPos, behavior: 'smooth'});        
    }
    let scrollNav = (slidePos) => {
        var nav = document.getElementsByClassName('nav');
        if (carouselNav && nav.length > 1 && nav[slidePos]) {
            fader(null,slidePos);
            scroller(slidePos);
        }
    }
    let slider = (e) => {
        if (e.target.alt) {
            var slide = e.target.alt.replace('thumbnailImg-', '');
            if (carouselContent) {
                if (e.currentTarget.className.indexOf('nav') !== -1) {
                    fader(e, null);
                    slidePos = parseInt(slide, 10);
                    scroller(slidePos);
                }
            }
        }
    }
    let slideLeft = () => {
        if (slidePos > 0) {
            slidePos-=1;
            scrollNav(slidePos);
        }
    }
    let slideRight = () => {
        slidePos+=1;
        if (slidePos < document.getElementsByClassName('nav').length) {
            scrollNav(slidePos);
        }
    }
    onMount(() => {
        var contentWidth = carouselWidth.offsetWidth / entries.length;
        if (contentWidth < 610 && document.querySelector('.zoomImg')) {
            var zoomImgs = document.getElementsByClassName('zoomImg');
            zoomImgs = [].slice.call( zoomImgs );
            zoomImgs.forEach(zoomImg => {
                zoomImg.classList.add('quickview');
            });
            isQuickview = true;
        }
    })
</script>

<div class="base-carousel" style="{isQuickview ? 'width:500px;' : ''}" bind:this={baseCarousel}>
    <div class="carousel-holder" bind:this="{carouselContent}">
        {#if typeof entries === 'object' && entries.data && entries.data.products}
            <ArrowCarouselLeftNewIcon bind:this="{arrowLeft}" />
            {#each entries.data.products as Product}
                <svelte:component this={entries.component} Product={ Product } />
            {/each}
            <ArrowCarouselRightNewIcon bind:this="{arrowRight}" />
        {:else if typeof entries === 'string'}
            <slot />
        {:else if entries && Array.isArray(entries) && entries.length}
            <div class="carousel-content" bind:this="{carouselWidth}">
                {#each entries as entry, i}
                    <div class="zoomImg" bind:this="{i}">
                        <ZoomImg normalImage="{ entry.imgUrl }" zoomedImage="{ entry.zoomUrl }" {width} {height} {style} />
                    </div>
                {/each}
            </div>
        {/if}
    </div>
    {#if entries && Array.isArray(entries) && entries.length > 1 && entries[0].thumbUrl}
        <div class="nav-bar">
            <span on:click={slideLeft} style="transform: rotate(180deg);"><ArrowDownIcon /></span>
            <div class="carousel-nav" bind:this="{carouselNav}">
                <div class="nav-holder">
                    {#each entries as nav, j}
                        <div class="nav{j===0 ? ' fade': ''}" on:click={slider}>
                            <img class="thumbImage" src="{ nav.thumbUrl }" alt="thumbnailImg-{j}" />
                        </div>
                    {/each}
                </div>
            </div>
            <span on:click={slideRight}><ArrowDownIcon /></span>
        </div>
    {/if}
</div>

