<script>
    import { createEventDispatcher } from 'svelte';
    import { slide } from 'svelte/transition';
    import { setupI18n, t } from '../services/i18n.js';
    import { ArrowAccordion, DrawerPlusIcon, DrawerMinusIcon } from '../SVG';
    export let entry;
    let isOpen = false,
        productIdx = 0,
        productTitle = '',
        cats,
        depts,
        pType,
        listLength;
    if (Array.isArray(entry) && entry.length) {
        productTitle = entry[0];

        if (entry.length > 1 && Array.isArray(entry[1]) && entry[1].length) {
            pType = entry[1][0].facetType;
            listLength = entry[1].length;
        }
    }
    const dispatch = createEventDispatcher();
    const toggle = () => {
        isOpen = !isOpen;
        dispatch('accordionClicked', {
            isOpen: isOpen
        });
    };
    const toggleCats = () => {
        var allCats = document.getElementsByClassName('hierarchy');
        var allCatsArr = [].slice.call(allCats);
        productIdx = 0;
        allCatsArr.forEach((cat) => {
            if (cat.classList.contains('collapsed')) {
                cat.classList.remove('collapsed');
                cats.innerHTML = 'less';
            } else if (productIdx > 6) {
                cat.classList.add('collapsed');
                cats.innerHTML = 'all';
            }
            productIdx++;
        });
    };
    const toggleDepartments = () => {
        var allDepts = document.getElementsByClassName('dept');
        var allDeptsArr = [].slice.call(allDepts);
        allDeptsArr.forEach((dept) => {
            if (dept.classList.contains('collapsed')) {
                dept.classList.remove('collapsed');
                depts.innerHTML = 'less';
            } else {
                dept.classList.add('collapsed');
                depts.innerHTML = 'all';
            }
        });
    };

    setupI18n();
</script>

{#if Array.isArray(entry) && entry.length > 1}
    <button class="toggle-accordion" on:click={toggle} aria-expanded={isOpen}>
        {productTitle}
        {#if pType === 'content'}
            {#if !isOpen}
                <DrawerPlusIcon size="15" />
            {:else}
                <DrawerMinusIcon size="15" />
            {/if}
        {:else if pType !== 'product'}
            <ArrowAccordion size="15" />
        {/if}
    </button>
    {#if isOpen || pType === 'product'}
        <ul transition:slide={{ duration: 300 }}>
            {#each entry[1] as { facetName, facetType, facetUrl, facetCount, facetSelected, facetIndexable, content }, i}
                {#if facetType === 'product'}
                    <li class={facetSelected}>
                        <a href={facetUrl}>
                            <span>
                                {facetName}
                            </span>
                        </a>
                    </li>
                {:else if facetType === 'box' || facetType === 'disc'}
                    <li class={facetSelected}>
                        <a
                            id="facetNavSize-{facetName.toLowerCase()}"
                            href={facetUrl}
                            title="Refine by:{facetName}"
                            rel="follow"
                            isindexablecategory={facetIndexable}
                        >
                            <i class={facetType === 'box' ? 'box' : 'disc'} />
                            <span class="visually-hidden">{$t('core.accordion.refine')} {facetName}: </span>
                            <span class="refinement-value">
                                {facetName} <span class="filter-count">({facetCount})</span>
                            </span>
                        </a>
                    </li>
                {:else}
                    <slot name="content">{@html content}</slot>
                {/if}
            {/each}
        </ul>
    {/if}
    {#if pType === 'product'}
        {#if listLength > 7}
            <button class="all-cats" on:click={toggleCats}>View <span bind:this={cats}>all</span> {productTitle}</button
            >
        {/if}
        <!-- TODO: pipe in categories API -->
        <ul>
            <li class="dept collapsed">
                <a class title="Baby + Toddler" href="/product-list-page/c/baby-and-toddler/">
                    <span>Baby + Toddler</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Bathroom Furniture & Decor" href="/product-list-page/c/bathroom/">
                    <span>Bathroom Furniture & Decor</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Bedding" href="/product-list-page/c/bedding/">
                    <span>Bedding</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Deals" href="/product-list-page/c/deals/">
                    <span>Deals</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Decor + Pillows" href="/product-list-page/c/home-decor/">
                    <span>Decor + Pillows</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Furniture" href="/product-list-page/c/furniture/">
                    <span>Furniture</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Home Storage Solutions" href="/product-list-page/c/storage/">
                    <span>Home Storage Solutions</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Kids" href="/product-list-page/c/kids/">
                    <span>Kids</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Kitchen" href="/product-list-page/c/kitchen/">
                    <span>Kitchen</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Lighting" href="/product-list-page/c/lighting/">
                    <span>Lighting</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Mattresses" href="/product-list-page/c/mattresses/">
                    <span>Mattresses</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Outdoor" href="/product-list-page/c/outdoor/">
                    <span>Outdoor</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Rugs" href="/product-list-page/c/rugs/">
                    <span>Rugs</span>
                </a>
            </li>
            <li class="dept collapsed">
                <a class title="Shop By" href="/product-list-page/c/shop-by/">
                    <span>Shop By</span>
                </a>
            </li>
        </ul>
        <button class="all-cats" on:click={toggleDepartments}
            >View <span bind:this={depts}>all</span> departments</button
        >
    {/if}
{/if}

