import { trimHelper } from '../utils';

const naPhone = /^([2-9][0-8][0-9])+[\-]?([2-9][0-9]{2})[\-]?([0-9]{4})(\s*x[0-9]+)?$/;
export const regex = {
    phone: {
        us: naPhone,
        ca: naPhone,
        fr: /^0[1-6]{1}(([0-9]{2}){4})|((\s[0-9]{2}){4})|((-[0-9]{2}){4})$/,
        it: /^(([0-9]{2,4})([-\s\/]{0,1})([0-9]{4,8}))?$/,
        jp: /^(0\d{1,4}- ?)?\d{1,4}-\d{4}$/,
        cn: /.*/,
        gb: /^((\(?0\d{4}\)?\s?\d{3}\s?\d{3})|(\(?0\d{3}\)?\s?\d{3}\s?\d{4})|(\(?0\d{2}\)?\s?\d{4}\s?\d{4}))(\s?\#(\d{4}|\d{3}))?$/
    },
    postal: {
        us: /^\d{5}(-\d{4})?$/,
        ca: /^[ABCEGHJKLMNPRSTVXY]{1}\d{1}[A-Z]{1} *\d{1}[A-Z]{1}\d{1}$/,
        fr: /^(F-)?((2[A|B])|[0-9]{2})[0-9]{3}$/,
        it: /^([0-9]){5}$/,
        jp: /^([0-9]){3}[-]([0-9]){4}$/,
        cn: /^([0-9]){6}$/,
        gb: /^([A-PR-UWYZ0-9][A-HK-Y0-9][AEHMNPRTVXY0-9]?[ABEHMNPRVWXY0-9]? {1,2}[0-9][ABD-HJLN-UW-Z]{2}|GIR 0AA)$/
    },
    notCC: /^(?!(([0-9 -]){13,19})).*$/,
    email: /^(?:[a-zA-Z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-zA-Z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-zA-Z0-9](?:[a-zA-Z0-9-]*[a-zA-Z0-9])?\.)+[a-zA-Z0-9](?:[a-zA-Z0-9-]*[a-zA-Z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-zA-Z0-9-]*[a-zA-Z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])$/,
    creditcardcvn: /^[0-9]{3,4}$/,
    oneUpLetter: /^.*[A-Z].*$/,
    oneLowLetter: /^.*[a-z].*$/,
    oneDigit: /^.*[0-9].*$/,
    oneSpecialChar: /^.*[`~!@#$%^&*()_+=\-[\]|}{;'":,.\/?><\\].*$/,
    validChar: /^[`~!@#$%^&*()_+=\-[\]|}{;'":,.\/?><\\ A-Za-z0-9]+$/,
    notDigitDashSpace: /[^0-9-\s]+/,
    numberSymbol: /[^\w\s-.]|[0-9]/,
    lettersHypen: /^[a-zA-Z|-]+$/,
    letters: /^[a-zA-Z]+$/,
    lastname: /^[\w-\d\s\u00C0-\u017F]+$/
};

/**
 * Validates if a given password matches password policies.
 * @param {String} value The password string to be validated
 * @returns true if valid. Otherwise, false
 */
export const validatePassword = value => {
    value = trimHelper(value);

    return regex.oneUpLetter.test(value) && 
           regex.oneLowLetter.test(value) && 
           regex.oneDigit.test(value) && 
           regex.oneSpecialChar.test(value) &&
           regex.validChar.test(value);
}

/**
 * Validates a given US phone number against the US phone regex
 * @param {String} value 
 * @returns 
 */
export const validateUSPhone = value => regex.phone.us.test(trimHelper(value))

/**
 * Validates a given US postal code number against the US postal code regex
 * @param {String} value The postal code which will be validated
 * @returns true if valid. Otherwise, false
 */
export const validateUSPostalCode = value => regex.postal.us.test(trimHelper(value))

/**
 * Validates the correct format for the year of purchase
 * @param {String} value Year of purchase to be validated
 * @returns true if valid. Otherwise, false
 */
export const validateYearOfPurchase = value => {
    let currentYear = new Date().getFullYear();
    let inputYear = Number(value);

    return !(regex.notDigitDashSpace.test(value) || 
           value.length < 4 || 
           inputYear > currentYear);
}

/**
 * Validates that a credit card owner is not a credit card number
 * @param {String} value The owner field to be validated
 * @returns true if valid. Otherwise, false
 */
export const validateOwner = value => regex.notCC.test(trimHelper(value))

/**
 * Validates that a special characters are disallowed.
 * Allowed characters should be hyphen, space, period and letters.
 * @param {String} value The field which will be validated
 * @returns true if valid. Otherwise, false
 */
export const validateSpecialCharacters = value => !regex.numberSymbol.test(value)

/**
 * Validates the given email address
 * @param {String} value The email field which will be validated
 * @returns true if valid. Otherwise, false
 */
export const validateEmail = value => regex.email.test(trimHelper(value))

/**
 * Validates the given email address and that the two emails are equal 
 * @param {String} value The email field which will be validated
 * @param {String} email The email to compare against the validated email
 * @returns true if valid. Otherwise, false
 */
export const validateConfirmEmail = (value, email) => {
    value = trimHelper(value);

    return regex.email.test(value) && value.toLowerCase() === email.trim().toLowerCase();
}

/**
 * Validates the given passwords are equal
 * @param {String} value The password field which will be validated
 * @param {String} password The password to compare against the validated password
 * @returns true if valid. Otherwise, false
 */
export const validateConfirmPassword = (value, password) => value.trim() === password.trim();

/**
 * Validates the given credit card number
 * @param {String} value The creditcard field which will be validated
 * @returns true if valid. Otherwise, false
 */
export const validateCreditCard = value => {
    // accept only digits, dashes or spaces 
    if (regex.notDigitDashSpace.test(value)) return false;

    // The Luhn Algorithm. It's so pretty.
    let nCheck = 0, bEven = false;
    value = value.replace(/\D/g, '');
    
    for (let n = value.length - 1; n >= 0; n--) {
        let cDigit = value.charAt(n),
            nDigit = parseInt(cDigit, 10);

        if (bEven) {
            if ((nDigit *= 2) > 9) nDigit -= 9;
        }

        nCheck += nDigit;
        bEven = !bEven;
    }

    return nCheck % 10 == 0;
}

/**
 * Validates the month and year of a credit card expiration date
 * @param {String | Number} month The credit card expiration month
 * @param {String | Number} year The credit card expiration year
 * @returns true if valid. Otherwise, false
 */
export const validateCreditCardExpirationDate = (month, year) => {
    let current = new Date(),
        currentMonth = current.getMonth() + 1,
        currentYear = current.getFullYear();
    month = parseInt(month, 10);
    year = parseInt(year, 10);

    return (isNaN(month) || isNaN(year)) || 
           (year === currentYear && month >= currentMonth) || 
           year > currentYear;
}

/**
 * Validates the given credit card cvn number
 * @param {String} value The cvn field which will be validated
 * @returns true if valid. Otherwise, false
 */
export const validateCreditCardCVN = value => regex.creditcardcvn.test(value);