<script>
    import { ArrowSelectIcon } from '../SVG';
    import { createEventDispatcher } from 'svelte';
    export let name,
        className = '',
        items,
        dataAttr;
    const dispatch = createEventDispatcher();
</script>

{#if name && items}
<div class="select-style" data-testid="mainSelect">
    <select
        {name}
        id={name}
        class={className}
        {dataAttr}
        on:change={(e) => {
            dispatch('selected', e.target.value);
        }}>
        {#each items as { value, label, option, selected }}
            <option {value} {label} {selected}>
                {option}
            </option>
        {/each}
    </select>
    <div class="select-svg"><ArrowSelectIcon /></div>
</div>
{/if}

