<script>
	import { Swipe, SwipeItem } from "svelte-swipe";
	import ZoomImg from '../ZoomImg/ZoomImg.svelte';

	let active_item = 0;
	let SwipeComp;

	const changeSlide = (i) => SwipeComp.goTo(i);
	const nextSlide = () => SwipeComp.nextItem();
	const prevSlide = () => SwipeComp.prevItem();

	export let images, imageType;
</script>

<div class="swipe-holder">
	<div class="img">
		{#if images}
			<Swipe bind:active_item bind:this={SwipeComp}>
		{#each images as image}
			<SwipeItem>
				<ZoomImg normalImage={image+imageType.main} zoomedImage={image+imageType.zoom}/>
			</SwipeItem>
		{/each}
			</Swipe>
		{/if}
	</div>
  
	<div class="arrows">
		{#if images}
			<button type="button" on:click={prevSlide}></button>
			{#each images as image, i}
				<img class="{active_item == i ? 'active' : ''}" on:click={() => changeSlide(i)} src={image+imageType.grid} alt="">
			{/each}
			<button type="button" on:click={nextSlide}></button>
		{/if}
	</div>
</div>

