<script>
    export let normalImage,
      zoomedImage,
      currentImage = normalImage,
      width = '685px',
      height = '420px',
      style = '';

      let x, y, backgroundSize;
  
    function setZoom(clientX, clientY, image) {
      x = clientX;
      y = clientY;
      currentImage = image;
    }
  
    function handleZoomEvent(e) {
      backgroundSize = e.type == 'mouseleave' ? '' : '195%';
      switch (e.type) {
        case "mousemove":
          setZoom(-e.offsetX, -e.offsetY, zoomedImage);
          break;
        case "mouseleave":
          setZoom(0, 0, normalImage);
          break;
        case "mousedown":
          setZoom(-e.offsetX, -e.offsetY, zoomedImage);
          break;
        case "mouseup":
          setZoom(0, 0, normalImage);
          break;
        case "touchmove":
          setZoom(-e.touches[0].clientX, -e.touches[0].clientY, zoomedImage);
          break;
        case "touchstart":
          setZoom(-e.touches[0].clientX, -e.touches[0].clientY, zoomedImage);
          break;
        case "touchend":
          setZoom(0, 0, normalImage);
          break;
      }
    }
  </script>

  <div
    class="zoom-handler"
    on:mousedown={handleZoomEvent}
    on:mouseup={handleZoomEvent}
    on:mousemove={handleZoomEvent}
    on:mouseleave={handleZoomEvent}
    on:touchstart={handleZoomEvent}
    on:touchmove={handleZoomEvent}
    on:touchend={handleZoomEvent}
    style="background-image: url({currentImage}); background-size:{backgroundSize}; background-position-x:{x}px; background-position-y:{y}px; width:{width}; height:{height}; {style}" 
    data-testid="mainZoomImg"
  />
  
  <style  src="./zoomimg.scss">/* Colors */
