<script>
    import Select from '../../Select/Select.svelte';

    export let variationValues,
            selectedValue,
            variationAttribute,
            selectable;

    let items = []
    if (selectable) {
        for (let variation of variationValues) {
            let item = {};
            item.value = variation.linkUrl;
            item.label = variation.displayValue;
            item.selected = variation.selected;
            item.option = variation.displayValue;
            items.push(item);
        }
    }
</script>

<div>
    {#if selectable}
        <Select name="selectable-swatches" className="{variationAttribute + ' ' + selectedValue}" items={items} />
    {:else}
        <ul class="variant-swatches {selectedValue}">
            {#each variationValues as {variantName, displayValue, selected, linkUrl, imgUrl}}
                <li class="{variationAttribute}" class:active={selected}>
                    <a class="swatch-anchor" href="{linkUrl}" title="{variantName + ': ' + displayValue}">
                        {#if imgUrl}
                            <img src="{imgUrl}" alt="{displayValue}">
                            <p class="variation-name">{displayValue}</p>
                        {/if}
                    </a>
                </li>
            {/each}
        </ul>
    {/if}
</div>

