<script>
    import { createEventDispatcher } from 'svelte';
    export let radioGroup;

    let isActive;

    const dispatch = createEventDispatcher();

    const sendUnbxd = (e) => {
        isActive = e.target.value;
        dispatch('checked', isActive);
    }
</script>

{#if radioGroup}
    {#each radioGroup as { className, radioId, disabled, name, isChecked, value, required, dataUrl, dataSwatchImg, dataSwatchAlt, dataTitle='', dataLgImg }}
    <label for={radioId} data-url={dataUrl} data-title={dataTitle} data-lgImg={dataLgImg} data-testid={value=="$1000+" && "facet2" || undefined}>
        <input type="radio" {name} id={radioId} class={className} {value} {required} checked={isChecked ? 'checked' : null} {disabled} on:change={sendUnbxd} />
        {#if dataSwatchImg}
            <img src={dataSwatchImg} alt={dataSwatchAlt} data-checked={isChecked} />
        {:else}
            <span>{value} {#if dataTitle}<span class="facetQty">({dataTitle})</span>{/if}</span>
        {/if}
    </label>
    {/each}
{/if}

