<script>
    import { onMount } from "svelte";

    export let query, dir = 'max';

    let mediaQuery;
    let mediaQueryListener;
    let wasMounted = false;
    let matches = false;
    let testQuery = '(max-width: ';

    onMount(() => {
        if (dir === 'min') {
            testQuery = '(min-width: ';
        }
        switch(query) {
            case 'xsm':
                testQuery += '300px)';
                break;
            case 'sm':
                testQuery += '320px)';
                break;
            case 'mxsm':
                testQuery += '350px)';
                break;
            case 'md':
                testQuery += '480px)';
                break;
            case 'mmd':
                testQuery += '500px)';
                break;
            case 'lg':
                testQuery += '768px)';
                break;
            case 'dsk':
                testQuery += '1025px)';
                break;
            case 'con':
                testQuery += '1280px)';
                break;
            default:
                testQuery += '1280px)';
                break;
        }
        wasMounted = true;
        return () => {
            removeActiveListener();
        };
    });

    $: {
        if (wasMounted) {
            removeActiveListener();
            addNewListener(testQuery);
        }
    }

    function addNewListener(testQuery) {
        mediaQuery = window.matchMedia(testQuery);
        mediaQueryListener = v => matches = v.matches;
        mediaQuery.addListener(mediaQueryListener);
        matches = mediaQuery.matches;
    }

    function removeActiveListener() {
        if (mediaQuery && mediaQueryListener) {
            mediaQuery.removeListener(mediaQueryListener);
        }
    }
</script>

<slot {matches} />