<script>
    import { getContext } from 'svelte';
    const { content_slab } = getContext('Pager') ? getContext('Pager') : {content_slab: undefined};
    export let component;
</script>

{#if content_slab}
    {#each $content_slab as data}
        <svelte:component this={component} Product={data} />
    {/each}
{/if}
